.h2{
  color: aqua;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  animation:Glow 2s infinite;
}


@keyframes Glow{
  0%{
    opacity: 0%;
    scale: 0;
  }
  
 10%{
    opacity: 100%;
    scale: 1;
 }
}
  .h2{
    animation: Glow 1s linear infinite;
  }
  
  .animated {
    animation: Glow 1s linear infinite;
  }

