 ::-webkit-scrollbar{
  background-color: rgb(49, 49, 49);
  height: 10px; 
  border-radius: 10px;
  display: none;
 }

 ::-webkit-media-controls-play-button,
 ::-webkit-media-controls-pause-button {
  display: none;
  /*width:10px;
  height: 15px;
  border-radius:20%;
