}

::-webkit-media-controls-play-button::before {
  content: '\f04b'; /* play icon */
  font-family: FontAwesome;
}
::-webkit-media-controls-pause-button::before {
  content: '\f04c'; /* pause icon */
  font-family: FontAwesome;
}

::-webkit-progress-bar{
